import React, { useRef, useEffect } from 'react';
import VanillaTilt from 'vanilla-tilt';
import EyeIcon from './icons/eye.inline.svg';

export const Eye = () => {
  const eyeRef = useRef();
  useEffect(() => {
    VanillaTilt.init(eyeRef.current, {
      reverse: true,
      'full-page-listening': true,
      perspective: 15,
      axis: 'x',
    });
  }, []);

  return (
    <div className="mylogo d-none d-lg-block">
      <div ref={eyeRef}>
        <EyeIcon />
      </div>
    </div>
  );
};
